body {
    overflow: hidden;
    margin: 0 auto;
    background-color: grey;
}

.App {
   
   position: center;
   display: flex;
   color: beige;
   justify-content: center;
   flex-direction: column;
    max-width: 100%;
    max-height: 100%
   
  
  }

  .animation{
      display: flex;
      justify-content: center;
  }

  .from {
    height: 30%;
    display: flex;
    justify-content: center; 
  }

  .to{
    height: 30%;
    display: flex;
    justify-content: center;
    
  }
  .from-and-to-input {
      display: flex;
      justify-content: space-around;
  
  }
  .from-and-to-labels{
     display: flex;
     justify-content: center; 
  }

  
  .amount {
      display: flex;
      justify-content: center;
        margin-bottom: -1%;
      margin-top:4em;  
  }
  .amount-input{
  
    margin-top: 10%;
  }

  .input-field{
   display: flex;
   justify-content: center;

  }
  
  .result {
         
     display: flex;
     justify-content: center;
      font-size: 1em;
      margin-bottom: 5%;
      font-family: 'Playfair Display', serif;
     
   
      
  }

  .result-submit{
     
      font-size: 1em;
     
      font-family: 'Playfair Display', serif;
      color: green;
  }
  .result-label{
      display: flex;
      margin-bottom: -20%;
      justify-content: center;
  }
  
  #coins{
  
     margin-bottom:2%;
     margin-top: 10%;
     width: 10%;
      height: 10%;
       border-radius: 50%;
  }
  .flag {  
    
      display: flex;
      justify-content: space-around;
      margin-bottom: -1%
  }
  
  .flag-from-div{
      margin-top: -3%;
      display: flex;
      justify-content: center;
      height: 4em
  }
  .flag-to-div{
    margin-top: -3%;
    display: flex;
    justify-content: center;
    height: 4em
}

  .flag-frome-img{
    display: flex;
    justify-content: center;
  }
  .flag-to-img{
    display: flex;
    justify-content: center;
  }

hr.top-line { 
    margin-top: -2em;
    margin-bottom: -5%;
    display: block;
    overflow: hidden;
    border-style: inset;
    border-width: 1px;
    width: 60%;
}
