h1{
    font-family: 'Playfair Display', serif;
  
    line-height: .4em
    /* https://stackoverflow.com/questions/2539207/how-to-change-the-strike-out-line-through-thickness-in-css */
    
}
h2{
   
    font-family: 'Playfair Display', serif;
    padding-bottom: 1em;
    color: beige;
}

h3 {
  display: inline;
  font-family: 'Playfair Display', serif;
}

ul {
    font-size: 20%;
    font-style: bold;
    margin-top: -15%;
    margin-bottom: -.2em;
    padding: .1em;
}
